import { Component, OnInit, OnDestroy } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { ISubscription } from 'rxjs/Subscription';
import { AuthService } from '../auth/auth.service';
import { SiteApiResponseUtilities } from '../shared/services/SiteApiResponse';
export class BaseAdminComponent implements OnInit, OnDestroy {
    
    private respUtils = new SiteApiResponseUtilities();
    public errors: string[];
    public msgs: any[] = [];
    private routerSub: ISubscription;

        constructor(
        private route: ActivatedRoute,
        private router: Router
        ) {

        this.routerSub = this.router.events.subscribe(
            () => window.scrollTo(0, 0)
        );
    }

    ngOnInit() {
     
    }

    ngOnDestroy() {
        if (this.routerSub) {
            this.routerSub.unsubscribe();
        }
    }

    onError(error) {
        const errors = this.respUtils.getErrors(error);

        if (errors.length === 0) {
            errors.push("An unknown error occurred. Please try again.");
        }

        this.errors = errors;
        console.log(this.errors);
    }

    onSuccess(){
        this.errors = [];
        this.msgs = [];
        this.msgs.push({severity:'success', summary:'Success Message', detail:'updated successfully.'});
    }

    onRequestFailed(){
        this.msgs = [];
        this.msgs.push({severity:'error', summary:'Error Message', detail:this.errors.toString()});
    }
  
}