\n    display: table;\n    clear: both;\n  }\n  footer[role=contentinfo] {\n    position: fixed;\n    width: 100%;\n    bottom: 0;\n    left: 0px;\n    margin: 0;\n  }\n}\n@media screen and (min-width: 900px) {\n  footer[role=contentinfo] {\n    position: fixed;\n    width: 100%;\n    bottom: 0;\n    left: 0px;\n    margin: 0;\n  }\n}\n/* Profile */\n.profile-dropdown {\n  display: inline-block;\n  position: relative;\n  background: #f2f2f2;\n  width: 100%;\n  margin: auto;\n  font-weight: bold;\n  cursor: pointer;\n  font-size: 1.3rem;\n  border-radius: 3px;\n  -webkit-user-select: none;\n  -moz-user-select: none;\n  -ms-user-select: none;\n  user-select: none;\n}\n.profile-dropdown * {\n  -webkit-user-select: none;\n  -moz-user-select: none;\n  -ms-user-select: none;\n  user-select: none;\n}\n.profile-dropdown input[type=\"checkbox\"] {\n  display: none;\n}\n.profile-dropdown input[type=\"checkbox\"]:checked ~ ul {\n  display: block;\n  -webkit-animation: pulse 0.5s;\n          animation: pulse 0.5s;\n}\n.profile-dropdown input[type=\"checkbox\"]:checked ~ img {\n  background: #2a3542;\n}\n.profile-dropdown input[type=\"checkbox\"]:checked ~ label {\n  background: #2a3542;\n}\n.profile-dropdown input[type=\"checkbox\"]:checked ~ label i {\n  color: #f2f2f2;\n}\n.profile-dropdown input[type=\"checkbox\"]:checked ~ label:after {\n  content: '';\n  position: absolute;\n  top: 100%;\n  right: calc(50% - 10px);\n  display: block;\n  border-style: solid;\n  border-width: 7px 10px 0 10px;\n  border-color: #2a3542 transparent transparent transparent;\n  width: 0;\n  height: 0;\n  z-index: 9;\n}\n.profile-dropdown img {\n  display: inline-block;\n  background: #d9d9d9;\n  height: 2.5rem;\n  vertical-align: middle;\n  margin-right: 1rem;\n  margin: 0.5rem 0.75rem 0.5rem 0.5rem;\n  border-radius: 50%;\n}\n.profile-dropdown span {\n  display: inline-block;\n  vertical-align: sub;\n  width: 125px;\n  margin-right: 2rem;\n  overflow: hidden;\n  white-space: nowrap;\n  text-over